import { SET_COLOR, SELECT_SQUARE } from '../actiontypes';

const INITIAL_STATE = {
	selectedColor: '#f22a2a',
	selectedTool: null
};

const toolsReducer = (state = INITIAL_STATE, action) => {
	switch(action.type) {
		case SET_COLOR:
			return {
				...state,
			}

		case SELECT_SQUARE: {
			return {
				...state,
				selectedTool: 'SQUARE'
			}
		}

		default: return state;
	}
}

export default toolsReducer;


